@font-face {
    font-family: NuevaStdBold;
    src: url("fonts/NuevaStd-Bold.otf");
}
@font-face {
    font-family: NuevaStd;
    src: url("fonts/NuevaStd.ttf");
}
@font-face {
    font-family: NuevaStdBoldCond;
    src: url("fonts/NuevaStd-BoldCond.otf");
}

$fz: 1rem;
$hfd_primary_color: #c94d5d;
$hfd_font_color: #616060;
$menu_header_font: 1rem;
$menu_paragraph_font: .75em;
$social_font: 1.5em;
$social_i_font: 1.75em;
$nav_bg: #c94e5d;

strong {
  font-family: NuevaStdBold;
}
.hidden800 {
  @media screen and (max-width: 800px) {
    display: none;
  }
}

.hfd_color {
  color: $hfd_primary_color;
  a &{
    color: $hfd_primary_color;
    &:hover {
      color: darken($hfd_primary_color, 5%);
    }
  }
}

.hfd_bg_color {
  background: $hfd_primary_color;
}

/**
 * global style
 */
 .btn-hfd {
    color: #fff;
    background-color: $hfd_primary_color;
    border-bottom: 4px solid darken($hfd_primary_color, 25%);
    &:hover{
      background-color: darken($hfd_primary_color, 10%);
      color: white;
    }
    &:focus {
      outline: none;
    }
    &:active {
      background-color: darken($hfd_primary_color, 20%);
      transform: translateY(5px);
      -webkit-transform: translateY(5px);
    }
 }
 .btn-hfd-link {
  font-weight: 400;
  color: $hfd_primary_color;
  background-color: transparent;
  &:focus {
    box-shadow: 0 0 0 0.1rem rgba(201, 77, 93, .4);
  }
 }

body {
  margin: 0 2.5rem;
  margin-bottom: 200px;
  color: $hfd_font_color;
  font-size: 18px;
  font-family: NuevaStd;
  @media screen and (max-width: 800px) {
    margin: 0 .5rem;
    font-size: 15px;
  }
}
h1, h2, h3, h4, h5{
  font-family: NuevaStdBold;
}
.bm {
  margin-bottom: 400px;

}
* {
  box-sizing: border-box;
}

/**
 * Header
 */
.top__area {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}
.logo {
  img {
    width: 250px;
  }
}

.top__social {
  position: relative;
  text-align: center;
}
.social {
  font-size: $social_font;
  position: absolute;
  top: 0;
  right: 0;
  @media screen and (max-width: 800px) {
    position: static;
    font-size: $fz;
    margin-bottom: 10px;
  }
  i {
    font-size: $social_i_font;
  }
  a {
    color: #858585;
    text-decoration: none;
    &:hover {
      color: #ca3046;
    }
  }
}

/**
 * Footer
 */
 footer {
  text-align: center;
  position: fixed;
  height: 160px;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  width: 100%;
  // background: url('../images/icons/home_page_footer.png') no-repeat center center;
  background: $nav_bg;
  background-size: 300%;
  z-index: 2;
  @media screen and (max-width: 800px) {
    height: 80px;
  }
  @media screen and (max-width: 1199px) {
    height: 110px;
  }
  nav {
    display: flex;
    padding: 0 40px;
    @media screen and (max-width: 800px) {
      padding: 0 10px;
      overflow: auto;
      white-space: nowrap;
      display: inline-block;

    }
    a {
      margin-right: 10px;
      color: white;
      text-decoration: none;
      position: relative;
      @media screen and (max-width: 800px) {
        display: inline-block;
        margin-right: 10px;
        padding-right: 10px;
      }
      &:after {
        content: "";
        width: 2px;
        height: 30px;
        background: rgba(255, 255, 255, .6);
        position: absolute;
        top: 40%;
        right: -5px;
        @media screen and (max-width: 800px) {
          height: 20px;
          top: 30%;
        }
      }
      &:last-child:after {
        height: 0;
        width: 0;
      }
      h2 {
        font-size: $menu_header_font;
        @media screen and (max-width: 800px) {
          font-size: $menu_paragraph_font;
        }
      }
      p {
        font-size: $menu_paragraph_font;
        @media screen and (max-width: 1199px) {
          display: none;
        }
      }
      &:hover {
        color: white;
      }
      img {
        width: 50px;
        @media screen and (max-width: 800px) {
          width: 40px;
        }
      }

      &.active {
        img {
          background: darken($hfd_primary_color, 2%);
        }
        h2 {
          color: lightpink;
        }
      }

    }
  }
 }


/**
 * Home page slider 
 */
a.item {
  text-decoration: none;
}
.item__inner {
  & > div {
    height: 400px;
    position: relative;
    @media screen and (max-width: 800px) {
      height: 350px;
    }
    @media screen and (max-width: 480px) {
      height: 300px;
    }
  }
  h1 {
    // color: #315084;
    color: #646464;
    text-decoration: none;
  }
}
.item__inner {
  h1.left {
    position: absolute;
    text-align: center;
    left: 200px;
    top: 100px;
  }
  h1.right {
    position: absolute;
    text-align: center;
    right: 200px;
    top: 110px;
  }
  h1.left, h1.right {
    @media screen and (max-width: 800px) {
      position: static;
      font-size: 1.5rem;
    }
    @media screen and (max-width: 480px) {
      position: static;
      font-size: 1rem;
    }
  }
  .the_dentist {
    background:  url("../images/homepage/dentist.png") no-repeat;
    background-position: 300px 0px;
    background-size: contain;
    @media screen and (max-width: 800px) {
      background-position: -30px 40px;
      background-size: 150%;
    }
    @media screen and (max-width: 480px) {
      background-position: -30px 40px;
      background-size: 130%;
    }
  }
  .self_evaluation {
    background: url(../images/homepage/self_evaluation.png) no-repeat;
    background-position: 60px -100px;
    background-size: 70%;
    @media screen and (max-width: 800px) {
      background-position: center;
      background-size: 100%;
    }
  }
  .dental_knowledge {
    background: url(../images/homepage/dental_knowledge.png) no-repeat;
    background-position: center center;
    background-size: cover;
    @media screen and (max-width: 800px) {
      background-position: -90px center;
      background-size: 100%;
    }
    @media screen and (max-width: 480px) {
      background-position: -140px center;
      background-size: 150%;
    }
  }
  .dental_hygiene {
    background: url(../images/homepage/hygiene.png) no-repeat 0px -80px;
    background-position: 0px -80px;
    background-size: 80%;
    @media screen and (max-width: 800px) {
      background-position: center;
    }
  }
  .our_treatment {
    background: url(../images/homepage/treatment.png) no-repeat;
    background-position: 0 -100px;
    background-size: 70%;
    @media screen and (max-width: 800px) {
      background-position: center;
    }
  }
}
/**
 * owl carousel overridden 
 */
 .owl-theme .owl-dots .owl-dot span {
  background: #ca4f5f;
 }
 .owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span {
  background: #d2233e;
 }
 .owl-theme .owl-nav.disabled+.owl-dots {
    margin-top: -30px;
    position: relative;
  }


/**
 * dentist page
 */
 #dentist {
  h2 {
    color: $hfd_primary_color;
  }
  img {
    max-height: 300px;
    max-width: 100%;
    margin: 10px auto;
    display: block;
  }
 }

/**
 * Treatments page
 */

#treatments {
  img {
    width: 50%;
    @media screen and (max-width: 800px) {
      width: 60%;
    }
    margin: 1em 0;
    &.icon {
      width: 70px;
    }
  }
  ul {
    margin: 0 10px;
    padding: 0;
    text-align: left;
    list-style-image: url('../images/treatments/dental_li.png');
  }
}

/**
 * Dental knowledge
 */
 #knowledge {
  a.headline {
    color: $hfd_font_color;
    text-decoration: none;
    &:hover {
      color: darken($hfd_font_color, 2%);
      text-decoration: none;
    }
  }
  img.icon {
    height: 50px;
  }
  img.main {
    width: 80%;
    max-width: 400px;
    margin-bottom: 40px;
    @media screen and (max-width: 480px) {
      width: 80%;
    }
  }
  a {
    color: $hfd_font_color;
    text-decoration: none;
    &:hover {
      color: $hfd_font_color;
    }
  }

 }

 /**
  * knowledge layout and collection 
  */
 #knowledge_layout {
  a {
    color: $hfd_font_color;
    text-decoration: none;
    &:hover {
      color: darken($hfd_font_color, 2%);
      text-decoration: none;
    }
  }
 }

/**
 * location page
 */
 #location {
  h4 {
    display: inline-block;
    border-bottom: 2px solid #666;
  }
  img.icon {
    width: 40px;
  }
 }

/**
 * appointment page
 */
 #appointment {
  img {
    max-width: 100%;
    height: 300px;
  }
  input {
    background: #f7f4f4;
    outline: none;
    border: none;
    margin-left: 5px;
    padding: 5px;
    min-width: 250px;
  }
  form button {
    border: 1px solid $hfd_primary_color;
    outline: none;
    color: $hfd_primary_color;
    padding: 0px 20px;
    margin: 5px;
    border-radius: 15px;
  }
 }

 /**
  * Evaluations
  */
  #evaluation {
    h2.page-header {
      margin-bottom: 80px;
    }
    img.main {
      margin-right: 50px;
      @media screen and (max-width: 800px) {
        margin-bottom: 30px;
        margin-right: 0;
      }
    }
  }

  /**
   * Evaluations summary
   */
 #evaluation-summary {
  .evaluation-question-content {
    margin-top: 50px;
    @media screen and (max-width: 800px) {
      margin-top: 20px;
    }
  }
  button {
    cursor: pointer;
    font-family: NuevaStdBold;
  }
  .question {
    margin-top: 30px;
    @media screen and (max-width: 800px) {
      margin-top: 10px;
    }
    .prev_next {
      color: white;
      background: $hfd_primary_color;
      border-bottom: 4px solid darken($hfd_primary_color, 20%);
      margin-top: 20px;
      padding: 5px 17px 0px;
      font-size: 20px;
      &:focus{
        outline: none;
      }
      &:active {
        -webkit-transform: translateY(5px);
        transform: translateY(5px);
        background-color: darken($hfd_primary_color, 5%);
      }
      &:disabled {
        background: gray;
        border-color: darkgrey;
      }
    }
    .yes_no {
     color: white;
      background: $hfd_primary_color;
      border-bottom: 4px solid darken($hfd_primary_color, 20%);
      border-radius: 10px;
      padding: 4px 26px 0;
      &:focus {
        outline: none;
      }
      &:active {
        -webkit-transform: translateY(5px);
        transform: translateY(5px);
        background-color: darken($hfd_primary_color, 5%);
      }
    }
    p.answer {
      margin: 8px 0 6px;
      color: $hfd_primary_color;
    }
  }
 }

 /**
  * treatments collections
  */

.treatments_collections {
  .image_container {
    text-align: center;
  }
  figure {
    text-align: center;
    border: 1px solid rgba(0, 0, 0, .2);
    display: inline-block;
    padding: 5px;
    margin: 10px; 
    img {
      max-width: 300px;
      border: 1px solid #eee;
    }
    figcaption {
      margin: 10px 0;
    }
  }
}


